import React, {useEffect, useState} from "react";
import s from "./PriceBitcoin.module.css"
import cs from "../../common/styles/styles.module.css";
import {useSelector} from "react-redux";
import {AppRootStateType} from "../../store/store";
import {bitcoinMainInformationType} from "../../store/appReducer";
import Charter from "../chart/Chart";

const PriceBitcoin: React.FC = () => {

  const [hoverMain, setHoverMain] = useState<boolean>(false)

  const {priceCurrent, dataPriceBTC, dataVolumeBTC, averageVolumeRelative} = useSelector<AppRootStateType, bitcoinMainInformationType>(
    store => store.app.bitcoinMainInformation)

  useEffect(() => {
    document.title = `${priceCurrent}`
  }, [priceCurrent])

  return (
    <>
      <span
        className={cs.commonFlexBody}
        onMouseEnter={() => setHoverMain(true)}
        onMouseLeave={() => setHoverMain(false)}
      >
        <span>BTC:</span>
        <span className={`${cs.commonHeaderButtonMain} ${s.buttonMain}`}>{priceCurrent}</span>
        <span></span>
        <span className={`${cs.commonHeaderButtonMain} ${s.buttonVolume}`}>{averageVolumeRelative} %</span>
      </span>
      {hoverMain &&
        <Charter dataPrice={dataPriceBTC} dataVolume={dataVolumeBTC}/>
      }
    </>

  )
}

export default PriceBitcoin